.abc
{
    align-self: center;
    display: grid;
    position: absolute;
    top:50%;
 left: 50%;
 transform: translateX(-50%) translateY(-50%);
    width: 1000px;
    height: 150px;
    border: 2cm;
    border-block-color: black;
    background-color:rgb(6, 213, 249);
    width: 35%;
    margin: auto;
    padding-top: 50px;
    padding-bottom: 50px;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 64px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
